* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn-container {
  justify-content: center;
  gap: 10px;
  margin-top: 50px;
  display: flex;
}

.btn-container button {
  cursor: pointer;
  background-color: #dedede;
  border: none;
  border-radius: 8px;
  padding: 12px 48px;
  font-size: 20px;
  font-weight: 500;
  transition: all .3s;
}

.btn-container button:hover {
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.timer-container {
  flex-direction: column;
  justify-content: start;
  gap: 12px;
  margin-left: 20px;
  display: flex;
}

.timer-btn input {
  width: 210px;
  padding: 4px 24px 4px 8px;
}

.timer-btn button {
  padding: 4px 12px;
}

.timer {
  gap: 24px;
  display: flex;
}

.value {
  font-size: 32px;
  font-weight: 600;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.label {
  font-size: 16px;
  font-weight: 500;
}

